import React from 'react';
import {Button, Modal} from 'react-bootstrap'
import PaymentIamPort from "./PaymentIamPort.js";
import Iamport from "react-iamport";

const Payment = (props) => {



    return (

        <Modal {...props}>
            <Modal.Header closeButton></Modal.Header>
            <Modal.Title> </Modal.Title>
            <Modal.Body>
                {props.localName}  {props.unitPrice} ?
            </Modal.Body>
            <Modal.Footer>
                <PaymentIamPort unitPrice={props.unitPrice} localName={props.localName}/>
                <Button variant="secondary" onClick={props.onHide}></Button>
            </Modal.Footer>
        </Modal>
    );
};

export default Payment;