import React, { useState, useEffect } from "react";
import "./LocalCurrencyAmount.css";
import { Table } from "react-bootstrap";
import { SearchBar } from "../../../items";
import { Line, Bar } from "react-chartjs-2";
import axios from "axios";

const LocalCurrencyAmount = () => {

  const [startDate, setStartDate] = useState("");
  const [endDate, setEndDate] = useState("");
  const [useStartDate, setUseStartDate] = useState("");
  const [useEndDate, setUseEndDate] = useState("");
  const [citySelect, setCitySelect] = useState("");
  const [useStatusSelect, setUseStatusSelect] = useState("");
  const [chartData, setChartData] = useState({});
  const [currencyName, setCurrencyName] = useState("");
  const [totalKeys,setTotalKeys] = useState([]);
  const [totalValues,setTotalValues] = useState([]);
  const [salesTotalKeys,setSalesTotalKeys] = useState([]);
  const [salesTotalValues,setSalesTotalValues] = useState([]);
  const [salesTotalChart,setSalesTotalChart] = useState({});
  const [useChart,setUseChart]=useState({});
  const [localSelect,setLocalSelect]=useState("");
  const [useTotalLocalKeys,setUseTotalLocalKeys] = useState([]);
  const [useTotalLocalValues,setUseTotalLocalValues] =useState([]);
  const [salesList,setSalesList] = useState([]);
 
  useEffect(()=>{
    axios
      .get(`http://localhost:8080/admins/sales/list`)
      .then((res)=>{
        setSalesList(res.data.sales)
      
      })
      .catch((err)=>{
        throw err;
      })
  },[]);

  useEffect(()=>{
    axios
      .get(`http://localhost:8080/admins/currency/month/total`)
      .then((res) => {
       
        const dataKey = [];
        const dataValue = [];
        Object.entries(res.data).forEach(([key, value]) => {
          dataKey.push(key);
          dataValue.push(value);
        });
        setTotalKeys(dataKey);
        setTotalValues(dataValue);
      })
      .catch((err)=>{
        throw err;
      })
  },[]);

  useEffect(() => {
    if(currencyName ==="") {
      axios
        .get(`http://localhost:8080/admins/voucher/sales-total`)
        .then((res)=>{
          const datakeys = [];
          const datavalues = [];
          console.log(res.data)
          Object.entries(res.data).forEach(([key,value])=>{
            datakeys.push(key);
            datavalues.push(value.unitPrice);
          });
          setSalesTotalKeys(datakeys);
          setSalesTotalValues(datavalues);
        })
        .catch((err)=>{
          throw err;
        })
    }
  },[]);

  useEffect(()=> {
    if(localSelect ==="") {
      axios
      .get(`http://localhost:8080/admins/useChart/total`)
      .then((res)=>{
        const dataKeys = [];
        const dataValues = [];
        Object.entries(res.data).forEach(([key,value])=>{
          dataKeys.push(key)
          dataValues.push(value)
        });
        setUseTotalLocalKeys(dataKeys);
        setUseTotalLocalValues(dataValues);
      })
      .catch((err) => {
        throw err;
      })
    }
  },[]);

  const color = () => {
    const letters = "0123456789ABCDEF";
    let color = "#";
    for (let i = 0; i < 6; i++) {
      color += letters[Math.floor(Math.random() * 16)];
    }
    return color;
  };

  useEffect(() => {
    
    setChartData({
      labels: totalKeys.sort(),
      datasets: [
        {
          data: totalValues,
          backgroundColor: [
            "rgb(153,153,153,0.2)",
            "rgb(255,153,000,1)",
            "rgb(255,255,000,1)",
            "rgb(000,153,000,1)",
            "rgb(000,000,204,1)",
            "rgb(000,000,051,1)",
            "rgb(102,000,102,1)",
            "rgb(102,204,255,1)",
          ],
        },
      ],
    })

    setSalesTotalChart({
      labels: salesTotalKeys.sort(),
      datasets: [
        {
          data: salesTotalValues,
          backgroundColor:salesTotalKeys.map((key) => color())
        },
      ],
    })
      
    setUseChart({
      labels: useTotalLocalKeys,
      datasets: [
        {
          data: useTotalLocalValues,
          backgroundColor: useTotalLocalKeys.map((key) => color())
        },
      ],
    })
  }, [totalKeys,
    totalValues,
    salesTotalValues,
    salesTotalKeys,
    useTotalLocalKeys,
    useTotalLocalValues]);

  const start_end_date = (e) => {
    e.preventDefault();

    if (startDate > endDate) {
      alert("  .");
      setEndDate("");
    }

    if (currencyName === "") {
      alert(` `);
    } else if (startDate === "" || endDate === "") {
      alert(` `);
    } else if (startDate.split("-")[0] !== endDate.split("-")[0]) {
      alert(`   .`);
    } else {
      axios
        .get(
          `http://localhost:8080/admins/voucher/name-list/${currencyName}/${startDate}/${endDate}`
        )
        .then((res) => {
          const monthLocalTotalKeys = [];
          const monthLocalTotalValues = [];

          Object.entries(res.data).forEach(([key, value]) => {
            monthLocalTotalKeys.push(key);
            monthLocalTotalValues.push(value.unitPrice);
          });

          setSalesTotalKeys(monthLocalTotalKeys);
          setSalesTotalValues(monthLocalTotalValues);
        })
        .catch((err) => {
          throw err;
        });
    }
  };

  const currencyNameCheck = (e) => {
    e.preventDefault();
    setCurrencyName(e.target.value);
  };

  const use_start_end_date = (e) => {
    e.preventDefault();

    if (useStartDate > useEndDate) {
      alert("  .");
      setUseEndDate("");
    } else if (useStartDate ==="" ||useEndDate ===""  ) {
      alert(` `);
    } else if(localSelect==="") {
      alert(` `);
    } else if(useStartDate.split("-")[0] !== useEndDate.split("-")[0]){ 
      alert(`   .`); 
    } else {
        axios 
        .get(`http://localhost:8080/admins/useChart/test/${localSelect}/${useStartDate}/${useEndDate}`)
        .then((res)=>{
            const useLocalKeys = [];
            const useLocalValues = [];

            Object.entries(res.data).forEach(([key,value])=>{
                useLocalKeys.push(key);
                useLocalValues.push(value);
            });
            setUseTotalLocalKeys(useLocalKeys);
            setUseTotalLocalValues(useLocalValues);
        })
        .catch((err)=>{
            throw err;
        });
    }
  };


  const handleSearch = (searchWord) => {
   
     if (citySelect==="") {
      alert(` `);
    } else if (useStatusSelect ==="") {
      alert('  ');
    } else if(searchWord === "") {alert(' ')}
    else {
       axios
        .get(`http://localhost:8080/admins/sales/search`,{
          params:{
            useStatusSelect:useStatusSelect,
            citySelect:citySelect,
            searchWord:searchWord
          }
        })
        .then((res)=>{
      
            setSalesList(res.data.sales)
        })
        .catch((err)=>{
            throw err;
        })
     }
  };

  return (
    <div>
      <h2 className="mt-4" style={{"text-align" : "center"}}> </h2>
      <div>
        <div className="currencyTotal-div-byCurrencyName">
          <div id="graph-title">
            <h5 className="currency-h5 font-weight-bold">
                 
            </h5>
            <h6 className="currencyTotal-h6">
              -  Id     
            </h6>
            <select
              id="currencyTotal-select-currency"
              value={currencyName}
              onChange={currencyNameCheck}
            >
              <option value="" selected></option>
              <option value=""></option>
              <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
            </select>
          </div>
          <div>
              <input
                className="currencyTotal-data-byCurrencyName"
                min="2020-01-01"
                type="month"
                value={startDate}
                onChange={(e) => setStartDate(e.target.value)}
              />
              <span> ~  </span>
              <input
                className="currencyTotal-data-byCurrencyName"
                min=""
                type="month"
                value={endDate}
                onChange={(e) => setEndDate(e.target.value)}
              />
              <input
                className="currencyTotal-button btn btn-outline-primary"
                type="submit"
                onClick={start_end_date}
                value=""
              />
            </div>
        </div>
        <div className="currencyTotal-Bar">
          <Bar
            data={salesTotalChart}
            width={80}
            height={50}
            options={{
              responsive: true,
              legend: { display: false },
              scales: {
                yAxes:[{
                  ticks:{
                 
                    callback: function(value) {
                      if(parseInt(value) >= 1000){
                        return Intl.NumberFormat().format(value)
                      } else {
                        return value;
                      }
                    }
                  }
                }]
              },
              tooltips: {
                displayColors: false,
                backgroundColor: "#0a6dff",
                  callbacks: {
                      label: function(tooltipItem) {
                      return Intl.NumberFormat().format(tooltipItem.yLabel)
                      }//function

            }//callbaks
          }
            }}
          />
        </div>
      </div>
      <div id="second-graph-line">
        <div className="currencyTotal-div">
          <div>
            <h5 className="font-weight-bold">  </h5>
            <h6 className="currencyTotal-h6">
              -/   
              <select
                id="userLocal-select"
                value={localSelect}
                onChange={(e) => setLocalSelect(e.target.value)}
              >
                <option value="" selected></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
              </select>
            </h6>
          </div>
          <div>
            <h6 className="currencyTotal-h5-input font-weight-bold">
               :
            </h6>
            <div className="useDate-input">
            <input
              className="currencyTotal-data-byCurrencyName"
              min="2020-01-01"
              type="date"
              value={useStartDate}
              onChange={(e) => setUseStartDate(e.target.value)}
            />
            <span>  ~  </span>
            <input
              className="currencyTotal-data-byCurrencyName"
              min=""
              type="date"
              value={useEndDate}
              onChange={(e) => setUseEndDate(e.target.value)}
            />
            <input
              className="currencyTotal-button btn btn-outline-primary"
              type="submit"
              onClick={use_start_end_date}
              value=""
            />
          </div>
           
          </div>
          <div className="currencyTotal-Bar-byState">
            <Bar data={useChart}
            options={{
              legend: {
                display: false,
              },
              // title: { text: "(:)",
              //  display: true,
              // position:'bottom',
              // fontColor:'blue'},
              scales: {
                yAxes:[{
                  ticks:{
                       beginAtZero:true,
                    callback: function(value) {
                      if(parseInt(value) >= 1000){
                        return Intl.NumberFormat().format(value)
                      } else {
                        return value;
                      }
                    }
                  }
                }]
              },
              tooltips: {
                displayColors: false,
                backgroundColor: "#0a6dff",
                  callbacks: {
                      label: function(tooltipItem) {
                      return Intl.NumberFormat().format(tooltipItem.yLabel)
                      }//function

            }//callbaks
          }

            }} />
          </div>
        </div>
        <div className="currencyTotal-div" id="second-grh">
          <h5 className="font-weight-bold">  </h5>
          <div>
            <Line
              data={chartData}
              width={100}
              height={50}
              options={{
                legend: {
                  display: false,
                },
             
                scales: {
                  yAxes:[{
                    ticks:{
                      
                      callback: function(value) {
                        if(parseInt(value) >= 1000){
                          return Intl.NumberFormat().format(value)
                        } else {
                          return value;
                        }
                      }
                    }
                  }]
                },
                tooltips: {
                  displayColors: false,
                  backgroundColor: "#0a6dff",
                    callbacks: {
                        label: function(tooltipItem) {
                        return Intl.NumberFormat().format(tooltipItem.yLabel)
                        }//function
  
              }//callbaks
            }
  
              }}
            />
          </div>
        </div>
      </div>
      <div>
        <div className="curreny-div">
          <h2>  </h2>
          <select
            id="localcurrency_cityselect"
            value={citySelect}
            onChange={(e) => setCitySelect(e.target.value)}
          >
            <option value="" selected></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
          </select>
          <select
            id="localcurrency_statusselect"
            value={useStatusSelect}
            onChange={(e) => setUseStatusSelect(e.target.value)}
          >
            <option value="" selected></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
          </select>
          <div>
            <div id="localCurrency_search_bar">
              <SearchBar onSearch={handleSearch} />
            </div>
          </div>
          <br />
          <Table className="Table">
            <tr>
              <th>No</th>
        
              <th></th>
              <th>  </th>
              <th></th>
              <th></th>
              <th> </th>
              <th> </th>
              
            </tr>
            <tbody>
            {salesList.map((info,i) => (
              <tr key={i}>
                <td>{i+1}</td>
                <td>{info.currencyState}</td>
                <td>{info.localCurrencyName}</td>
                <td>{info.userId}</td>
                <td>{info.salesDate}</td>
                <td>{info.useDate}</td>   
                <td>{info.cancelDate}</td> 
              </tr>
              )
            )}
            </tbody>
          </Table>
        </div>
      </div>
    </div>
  )

};

export default LocalCurrencyAmount;